<template>
  <div>
    <div class="dataHeader">
      <div class="dataHeaderTitle" v-if="scollFlag===true">
        <span>数据管理</span>
      </div>
      <van-nav-bar
      title="数据管理"
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
      <div class="headerTime">
        <div>
          <span>设备发送数据时间</span>
        </div>
      </div>
      <div class="updataTime">
        <span>{{time}}</span>
      </div>
    </div>
    <div style="padding:0 10px;margin-bottom:67px">
      <div class="navigationArea">
        <v-Statistics />
      </div>
      <div class="Navigate">
        <v-Navigate />
      </div>
    </div>
  </div>
</template>
<script>
import Navigate from "@/mapComponents/leftNavigation/index";
import Statistics from "@/mapComponents/leftView/index";
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      margin: "",
      scollFlag: false,
      time: "",
      token:""
    };
  },
  components: {
    "v-Navigate": Navigate,
    "v-Statistics": Statistics
  },
  methods: {
    TabFunc(val){
      // this.$parent.tabClose(val);
    },
    eventScoller(e) {
      console.log(e);
      var scrollTop = e.target.scrollHeight;
      var scTop = e.target.clientHeight;
      var top = e.target.scrollTop;
      if (top <= 0) {
        this.margin = "margin-top:50px;";
        this.scollFlag = false;
      } else if (top > 0) {
        this.margin = "";
        this.scollFlag = true;
      }
    }
  },
  async created() {
    // window.addEventListener("scroll", this.eventScoller, true);
    this.token = sessionStorage.getItem("token");
    var url = "/station/water-station/map";
    //   var _this = this;
    var res = await AppBace.request(url, "GET", {}, this.token);
    // debugger
    this.time = res.data.data[0].dataTime;
  },
  mounted() {},
  destroyed() {
    // window.removeEventListener("scroll", this.eventScoller, true);
  }
};
</script>
<style scoped>
.navigationArea {
  background: white;
  border-radius: 10px;
  /* height: 200px; */
  position: relative;
  top: -20px;
  box-shadow: 0 0 2px #ccc;
  padding: 10px 5px;
}
.dataHeader {
  background-color: #3572fe;
}
.dataHeaderTitle1 {
  height: 50px;
  color: white;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: -webkit-linear-gradient(to left, #328efb, #4abbfe);
  background: linear-gradient(to left, #328efb, #4abbfe);
}
.dataHeaderTitle {
  height: 50px;
  /* color: white; */
  color: #ffffff;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #3572fe;
  z-index: 999;
}
.headerTime {
  /* background: #3572fe; */
  color: white;
  /* margin-top: 50px; */
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  opacity: 1;
}
.updataTime {
  color: white;
  font-size: 18px;
  height: 60px;
  display: flex;
  /* align-items: center; */
  padding: 0 10px;
}
.dataHeader1 {
  background: #0067ff;
  background: -moz-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, #0067ff),
    color-stop(100%, #2bb5ff)
  );
  background: -webkit-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: -o-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: -ms-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: linear-gradient(to bottom, #0067ff 0%, #2bb5ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#2bb5ff',GradientType=0 );
}
.dataHeader {
  background: -webkit-linear-gradient(to left, #328efb, #4abbfe);
  background: linear-gradient(to left, #328efb, #4abbfe);
}
.Navigate {
  /* background: white; */
  position: relative;
  top: -10px;
}
</style>


