<template>
  <div>
    <!-- 底部tab标签 -->
    <div class="tabBarFixed" v-show="!showMapData">
      <div @click="HomePage(0)">
        <span :style="homeStyle">首页</span>
      </div>
      <div @click="HomePage(1)">
        <span :style="dataStyle">数据</span>
      </div>
      <div @click="HomePage(2)">
        <span :style="myStyle">我的</span>
      </div>
    </div>

    <!-- tabpage -->
    <div v-show="$store.state.tabBarPage===0">
      
      <v-Home  />
      
     

    </div>
    <div v-show="$store.state.tabBarPage===1">
      <v-Data />
    </div>
    <div v-show="$store.state.tabBarPage===2">
      <v-My />
    </div>
    <van-popup
      v-model="showMapData"
      position="bottom"
      :style="popupHeight"
      close-icon-position="top-right"
      :overlay="false"
      round
      closeable
      v-swipeup="nextPage"
      v-swipedown="prevPage"
      class="css3Trasition"
      :close-on-popstate="true"
      :lock-scroll="false"
      id="css3Trasition"
    >
      <div style="position:relative">
        <div class="headerTitle">
          <span>{{content.stationName===undefined||content.stationName===null?content.name:content.stationName}}</span>
        </div>
        <div class="popupDataContainer">
          <div class="subtext" style="margin-top:20px;">
            <span>{{content.address}}</span>
          </div>
          <div class="flexRow">
            <div>
              <span class="title">水质状况:</span>
              <span
                :style="content.waterInfo==='无水'?'color:red':content.waterInfo==='-'?'':(content.waterInfo==='超标'||content.waterInfo==='黄色')?'color:#db0000':'color:#2481ff'"
              >{{content.waterInfo===null?"--":(content.waterInfo==='超标'||content.waterInfo==='黄色')?"超标":content.waterInfo==="-"?"--":content.waterInfo}}</span>
              <!-- <span style="color:#2481ff">{{content.waterInfo}}</span> -->
            </div>
            <div v-show="user!=='manager'">
              <span class="title">设备状态:</span>
              <span style v-if="content.deviceStatus===1">正常</span>
              <!-- <span style="color:#2481ff" v-if="content.deviceStatus===1">正常</span> -->
              <span style="color:red" v-if="content.deviceStatus===0">异常</span>
              <!-- <span style="color:" v-if="content.deviceStatus===1">正常</span> -->
              <span style="color:red" v-if="content.deviceStatus===2">维护</span>
              <span style="color:red" v-if="content.deviceStatus===3">离线</span>
              <span style="color:red" v-if="content.deviceStatus===4">无水</span>
              <span style="color:red" v-if="content.deviceStatus===8">停运</span>
            </div>
          </div>
          <div class="mapContent">
            <div class="flexBox">
              <div>
                <span class="title">站点负责人:</span>
                <span>秦政</span>
              </div>
              <div @click="Callme(15910771731)">
                <span class="title">电话号码:</span>
                <span style="color:rgb(36, 129, 255)">15910771731</span>
              </div>
            </div>
            <div class="bottomNavi">
              <!-- @click="AddHeight" -->
              <div class="imageSize" style="color:#2481ff">
                <!-- <img src="static/map/details.png" alt /> -->
                <span v-show="!flag">上滑详情</span>
                <span v-show="flag">下滑关闭</span>
              </div>
              <div>
                <div
                  class="imageSize borderNavi"
                  @click="NaviTo(content.latitude,content.longitude,content.address)"
                >
                  <img src="static/map/navi.png" alt />
                  <span>导航</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- 数据详情 -->
        <div class="dataDetails" v-show="flag">
          <div class="warningHeaderTitle">
            <div>预警提醒</div>
            <div style="font-size:12px;color:black">
              <span
                v-show="lonkFor===false"
                style="color:rgb(36, 129, 255)"
                @click="lookWarning(content.stationName,content.dataTime,content.warningList)"
              >查看</span>
              <span style="color:grey" v-show="lonkFor===true">暂无</span>
            </div>
          </div>
          <!-- .slice(0,1) -->
          <div
            v-for="(item,index) in (showwarningNews===false?content.warningList:content.warningList)"
            :key="index"
            class="warningTypeContent"
          >
            <div class="flexBoxWarning">
              <span>预警类型 {{index+1}}</span>
              <span style="color:red">{{item.warningType}}</span>
            </div>
          </div>
        </div>
        <!-- 实时数据 -->
        <div class="dataDetails" v-show="flag">
          <div class="warningHeaderTitle">
            <div>
              <span>{{content.stationName}}</span>
            </div>
            <div style="color: rgb(146, 146, 146)">{{content.dataTime}}</div>
          </div>
          <div class="dataHeader flexBoxContentRealData">
            <div>因子</div>
            <div style="flex:4">监测值</div>
            <div>标准值</div>
          </div>
          <div class="dataValue">
            <div class="flexBoxContentRealData">
              <div>pH</div>
              <div style="flex:4">
                <span
                  :style="content.ph|filterPhStyle($store.state.factor.ph,$store.state.factor.ph1,content.waterInfo)"
                >{{content.ph | filterNews(huanbao,content.waterInfo,content.devicePh,"ph")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.ph===null?'color:':(content.ph>$store.state.factor.ph1||content.ph<$store.state.factor.ph)?'color:red':'color:'"
                >{{content.ph|filter(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.ph}}~{{$store.state.factor.ph1}}</span>
              </div>
            </div>
            <div class="flexBoxContentRealData" v-show="content.hasCod!==2">
              <div>CODcr</div>
              <div style="flex:4">
                <span
                  :style="content.cod|filterYellowtToStyle($store.state.factor.cod,content.waterInfo)"
                >{{content.cod|filterNews(huanbao,content.waterInfo,content.deviceCod,"cod")}}</span>
                <!-- <span :style="content.cod | filterYellowtToStyle($store.state.factor.cod,content.waterInfo)">{{}}</span>
                <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.cod===null?'':content.cod>$store.state.factor.cod*1.15?red:content.cod>$store.state.factor.cod&&content.cod<$store.state.factor.cod*1.15?orange:''"
                >{{content.cod|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.cod+' '+'mg/L'}}</span>
              </div>
            </div>
            <div class="flexBoxContentRealData">
              <div>氨氮</div>
              <div style="flex:4">
                <span
                  :style="content.ammoniaNitrogen | filterYellowtToStyle($store.state.factor.ammoniaNitrogen,content.waterInfo)"
                >{{content.ammoniaNitrogen|filterNews(huanbao,content.waterInfo,content.deviceAn,"ammoniaNitrogen")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.ammoniaNitrogen===null?'--':content.ammoniaNitrogen>$store.state.factor.ammoniaNitrogen*1.15?red:content.ammoniaNitrogen>$store.state.factor.ammoniaNitrogen&&content.ammoniaNitrogen<$store.state.factor.ammoniaNitrogen*1.15?orange:''"
                >{{content.ammoniaNitrogen|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.ammoniaNitrogen+' '+'mg/L'}}</span>
              </div>
            </div>

            <div class="flexBoxContentRealData">
              <div>总氮</div>
              <div style="flex:4">
                <span
                  :style="content.nitrogen|filterYellowtToStyle($store.state.factor.nitrogen,content.waterInfo)"
                >{{content.nitrogen|filterNews(huanbao,content.waterInfo,content.deviceN,"nitrogen")}}</span>
                <!-- <span
                  :style="content.nitrogen|filterYellowtToStyle($store.state.factor.nitrogen,content.waterInfo)"
                >{{content.nitrogen|filterNews(huanbao,content.waterInfo,content.deviceN,"nitrogen")}}</span>
                <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.nitrogen===null?'--':content.nitrogen>$store.state.factor.nitrogen*1.15?red:content.nitrogen>$store.state.factor.nitrogen&&content.nitrogen<$store.state.factor.nitrogen*1.15?orange:''"
                >{{content.nitrogen|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.nitrogen+' '+'mg/L'}}</span>
              </div>
            </div>

            <div class="flexBoxContentRealData">
              <div>总磷</div>
              <div style="flex:4">
                <span
                  :style="content.phosphorus|filterYellowtToStyle($store.state.factor.phosphorus,content.waterInfo)"
                >{{content.phosphorus|filterNews(huanbao,content.waterInfo,content.deviceP,"phosphorus")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.phosphorus===null?'--':content.phosphorus>$store.state.factor.phosphorus*1.15?red:content.phosphorus>$store.state.factor.phosphorus&&content.phosphorus<$store.state.factor.phosphorus*1.15?orange:''"
                >{{content.phosphorus|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.phosphorus+' '+'mg/L'}}</span>
              </div>
            </div>

            <div class="flexBoxContentRealData">
              <div>总铜</div>
              <div style="flex:4">
                <span
                  :style="content.cuprum | filterWater($store.state.factor.cuprum,content.waterInfo)"
                >{{content.cuprum | filterNews(huanbao,content.waterInfo,content.deviceCu,"cuprum")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.cuprum===null?'--':content.cuprum>$store.state.factor.cuprum?'color:red':'color:'"
                >{{content.cuprum|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.cuprum+' '+'mg/L'}}</span>
              </div>
            </div>

            <!-- nickel -->
            <div class="flexBoxContentRealData">
              <div>总镍</div>
              <div style="flex:4">
                <span
                  :style="content.nickel | filterWater($store.state.factor.nickel,content.waterInfo)"
                >{{content.nickel | filterNews(huanbao,content.waterInfo,content.deviceNi,"nickel")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.nickel===null?'--':content.nickel>$store.state.factor.nickel?'color:red':'color:'"
                >{{content.nickel|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.nickel+' '+'mg/L'}}</span>
              </div>
            </div>

            <div class="flexBoxContentRealData" v-show="content.hasCod!==1">
              <div>总铬</div>
              <div style="flex:4">
                <span
                  :style="content.chromium | filterWater($store.state.factor.chromium,content.waterInfo)"
                >{{content.chromium | filterNews(huanbao,content.waterInfo,content.deviceCr,"chromium")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.chromium===null?'--':content.chromium>$store.state.factor.chromium?'color:red':'color:'"
                >{{content.chromium|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.chromium+' '+'mg/L'}}</span>
              </div>
            </div>

            <div class="flexBoxContentRealData">
              <div>总氰化物</div>
              <div style="flex:4">
                <span
                  :style="content.prussiate|filterYellowtToStyle($store.state.factor.prussiate,content.waterInfo)"
                >{{content.prussiate|filterNews(huanbao,content.waterInfo,content.devicePr,"prussiate")}}</span>
                <!-- <span
                  :style="content.waterInfo=='无水'||content.deviceStatus===4?'color:red':content.prussiate===null?'--':content.prussiate>$store.state.factor.prussiate*1.15?red:content.prussiate>$store.state.factor.prussiate&&content.prussiate<$store.state.factor.prussiate*1.15?orange:''"
                >{{content.prussiate|filterOther(content.waterInfo,content.deviceStatus)}}</span>-->
              </div>
              <div>
                <span>{{$store.state.factor.prussiate+' '+'mg/L'}}</span>
              </div>
            </div>
            <div class="flexBoxContentRealData justycont">
              <div style="flex:1" v-show="systemStadiu==true">
                <span>设备状态</span>
              </div>
              <div style="flex:1" v-show="systemStadiu==false"></div>
              <div style="flex:4" v-show="systemStadiu==true">
                <span style="color:" v-if="content.deviceStatus===1">正常</span>
                <span style="color:red" v-if="content.deviceStatus===0">异常</span>
                <span style="color:red" v-if="content.deviceStatus===2">维护</span>
                <span style="color:red" v-if="content.deviceStatus===3">离线</span>
                <span style="color:red" v-if="content.deviceStatus===4">无水</span>
                <span style="color:red" v-if="content.deviceStatus===6">停运</span>
              </div>
              <div style="flex:4" v-show="systemStadiu==false"></div>
              <div style="flex:1">
                <span
                  style="color:rgb(36, 129, 255); border-bottom:1px rgb(36, 129, 255) solid;"
                  @click="toHistory(content.stationName)"
                >历史数据</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </van-popup>

    <van-dialog
      v-model="showUpdataApp"
      show-cancel-button
      @cancel="cancelUpdateApp"
      @confirm="confirmUpdate"
    >
      <!-- <img src="https://img.yzcdn.cn/vant/apple-3.jpg" /> -->
      <div class="updataHeader">
        <span>版本更新</span>
      </div>
      <div class="boxUpdata">
        <div v-if="showVersionUpdate">
          <div class="heightStyle">
            <span>更新的版本：</span>
            <span>{{versionData.version}}</span>
          </div>
          <div class="heightStyle" style="height:30px;">
            <span>版本解决了以下的问题：</span>
          </div>
          <div v-for="(item,index) in versionData.description" :key="index">
            <div class="heightStyle">
              <span>{{index+1}}、</span>
              <span>{{item}}</span>
            </div>
          </div>
        </div>
        <div v-if="!showVersionUpdate">
          <div class="heightStyle">
            <span>当前为最新版本：</span>
            <span style="color:rgb(50, 142, 251)">{{version}}</span>
          </div>
        </div>
      </div>
    </van-dialog>
    <van-action-sheet
      style="z-index:999"
      v-model="showApp"
      :actions="actions"
      cancel-text="取消"
      @cancel="onCancel"
      @select="onSelect"
      description="选择市场安装，请确保已经安装安智市场"
    />
    <!-- <van-popup v-model="show" round position="bottom" :style="{ height: '20%' }" /> -->
  </div>
</template>
<script>
import Home from "@/components/map/realMap";
import My from "@/components/tab/settings";
import Data from "@/components/data/data";
import AppBace from "../../../../static/ajax";
import GaoDe from "../../../../static/js/toGaodeMap/index";
import AppUpdata from "../../../../static/js/anzhiShop/index";
import Vue from "vue";
import { Dialog } from "vant";
import { Notify } from "vant";
import { ActionSheet } from "vant";

Vue.use(ActionSheet);
export default {
  data() {
    return {
      tabBarPage: 0,
      homeStyle: "",
      dataStyle: "border:none;color:rgb(88, 87, 87);",
      myStyle: "border:none;color:rgb(88, 87, 87);",
      colorBack: "border:none;color:rgb(88, 87, 87);",
      showMapData: false,
      content: "",
      popupHeight: "height:215px;",
      flag: false,
      showwarningNews: false,
      showUpdataApp: false,
      showVersionUpdate: false,
      appName: "",
      versionData: [],
      version: "",
      user: "",
      systemStadiu: false,
      lonkFor: false,
      red: "color:red",
      orange: "color:orange",
      showApp: false,
      actions: [{ name: "市场更新" }, { name: "本地更新" }],
      huanbao: false
    };
  },
  components: {
    "v-Home": Home,
    "v-My": My,
    "v-Data": Data
  },
  filters: {
    filter(value, water, deviceStatus) {
      //console.log("====",value);
      if (water !== null && water !== "-" && deviceStatus !== 4) {
        if (value !== null) {
          if (typeof value == "number") {
            return value.toFixed(3);
          } else {
            return value;
          }
        } else {
          return "--";
        }
      } else if (water == null || deviceStatus === 4) {
        return "无水";
      } else {
        return "--";
      }
    },
    filterOther(value, water, deviceStatus) {
      if (water !== null && water !== "-" && deviceStatus !== 4) {
        if (value !== null) {
          if (typeof value == "number") {
            return value.toFixed(3) + "mg/L";
          } else {
            return value + "mg/L";
          }
        } else {
          return "--";
        }
      } else if (water == null || deviceStatus == 4) {
        return "无水";
      } else {
        return "--";
      }
    },
    // 因子的显示内容
    filterNews(value, user, water, device, name) {
      //debugger
      //console.log("====",value);
      // 无水跟device的优先级？
      if (water === "无水") {
        return "无水";
      } else {
        if (user == true && device == 4) {
          return "无水";
        } else {
          if (device === 4) {
            return limitFlag[name];
          }
          if (device === 5) {
            return "--";
          }
          // if (user == true) {
          if (value !== null) {
            if (typeof value == "number") {
              return value.toFixed(3);
            } else {
              return value;
            }
          } else {
            return "--";
          }
          // }
        }
      }
    },
    // 不用判断微超标的几个因子
    filterWater(value, standed, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value > standed) {
          return (color = "color:red");
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
        return (color = "color:red");
      } else {
        if (value > standed) {
          return (color = "color:red");
        } else {
          return "";
        }
      }
    },
    // 判断微超标的颜色
    filterYellowtToStyle(value, standed, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value > standed && value < standed * 1.15) {
          return (color = "color:orange");
        } else if (value > standed * 1.15) {
          return (color = "color:red");
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
        return (color = "color:red");
      } else {
        if (value > standed && value < standed * 1.15) {
          return (color = "color:orange");
        } else if (value > standed * 1.15) {
          return (color = "color:red");
        } else {
          return "";
        }
      }
    },
    // 判断ph的颜色
    filterPhStyle(value, small, large, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value !== null) {
          if (value < small || value > large) {
            return (color = "color:red");
          } else {
            return "";
          }
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
        return (color = "color:red");
      } else {
        if (value !== null) {
          if (value < small || value > large) {
            return (color = "color:red");
          } else {
            return "";
          }
        } else {
          return "--";
        }
      }
    }
  },
  methods: {
    onSelect(item) {
      if (item.name == "市场更新") {
        AppUpdata.updataApp();
      } else {
        var http = this.appName;
        // var http = "8886/group1/M00/00/01/rBLi414YOk-ACLArASIf1vSN0Rk764.apk";
        window.location.href = "http://47.112.16.168:" + http;
      }
    },
    onCancel() {
      this.showApp = false;
      // Toast('cancel');
    },
    cancelUpdateApp() {
      this.$store.state.upDateStatus = true;
    },

    confirmUpdate() {
      // 去到应用市场
      // AppUpdata.updataApp();
      this.$store.state.upDateStatus = true;
      this.showApp = true;
    },
    // 去到历史数据
    toHistory(name) {
      this.$router.push({
        path: "ToHistory",
        query: {
          stationName: name
        }
      });
    },

    Callme(number) {
      var _this = this;
      this.showMapData = false;
      Dialog.confirm({
        message: number,
        className: "dialogContent"
      })
        .then(() => {
          // on confirm
          window.location.href = "tel://" + number;
        })
        .catch(() => {
          // on cancel
          _this.showMapData = true;
        });
    },
    NaviTo(lng, lat, address) {
      var _this = this;
      this.showMapData = false;
      Dialog.confirm({
        message: "即将打开高德地图应用！"
      })
        .then(() => {
          GaoDe.toGaoDeMap("", "", "", lng, lat, address);
        })
        .catch(() => {
          _this.showMapData = true;
        });
    },
    HomePage(val) {
      this.$store.state.tabBarPage = val;
      console.log(val);
      switch (val) {
        case 0:
          this.homeStyle = "";
          this.dataStyle = this.colorBack;
          this.myStyle = this.colorBack;
          break;
        case 1:
          this.homeStyle = this.colorBack;
          this.dataStyle = "";
          this.myStyle = this.colorBack;
          break;
        default:
          this.homeStyle = this.colorBack;
          this.dataStyle = this.colorBack;
          this.myStyle = "";
          break;
      }
    },
    // 底部弹出框
    showMap(val, content) {
      if (val !== undefined && val !== "" && val !== null && content !== "") {
        this.showMapData = val;
        this.popupHeight = "height:215px;";
      } else if (val === false) {
        this.showMapData = val;
        this.popupHeight = "height:215px;";
      } else {
        this.showMapData = true;
        var arr = [];
        this.content = content;
        // console.log(content);
        if (content.warningList !== null && content.warningList.length > 1) {
          this.showwarningNews = true;
          this.lonkFor = false;
        } else if (
          content.warningList !== null &&
          content.warningList.length === 1
        ) {
          this.showwarningNews = false;
          this.lonkFor = false;
        } else {
          this.lonkFor = true;
        }
      }
    },
    AddHeight() {
      this.flag = !this.flag;
      if (this.flag) {
        this.popupHeight = "height:700 px";
      } else {
        this.popupHeight = "height:215px;";
      }
    },
    nextPage() {
      // setTimeout(() => {

      // }, 100);
      // debugger
      // this.popupHeight = "height:100%;";
      this.popupHeight = "height:700px;";
      setTimeout(() => {
        this.flag = true;
      }, 250);
      // setTimeout(() => {
      //   this.popupHeight = "height:"+$("#css3Trasition").height()+'px';

      // }, 400);
    },
    prevPage() {
      // setTimeout(() => {

      this.popupHeight = "height:215px;";
      setTimeout(() => {
        this.flag = false;
      }, 250);
    },
    lookWarning(name, time, warningList) {
      // Toast("");
      // Notify({ type: "warning", message: "正在测试请稍后！" });
      this.$router.push({
        path: "WarningDetailsnews",
        query: {
          stationName: name,
          time: time,
          type: warningList
        }
      });
    }
  },
  async created() {
    this.user = sessionStorage.getItem("user");
    if (this.user !== "manager") {
      this.systemStadiu = true;
      this.huanbao = false;
    } else {
      this.systemStadiu = false;
      this.huanbao = true;
    }
    var _this = this;
    var val = this.$store.state.tabBarPage;
    this.HomePage(val);
    if (this.$store.state.upDateStatus === false) {
      var token = sessionStorage.getItem("token");
      var url = "/table/appVersion";
      var res = await AppBace.request(url, "get", {}, token);
      if (
        res.data.data !== null &&
        res.data.data !== undefined &&
        res.data.data !== ""
      ) {
        var versionApp = localStorage.getItem("version");
        this.version = versionApp;
        var arr = res.data.data.version.split(".");
        var arr1 = versionApp.split(".");
        var currentVersion = arr1[0] + arr1[1] + arr1[2];
        var serviceVersion = arr[0] + arr[1] + arr[2];
        console.log(serviceVersion);
        if (Number(serviceVersion) > Number(currentVersion)) {
          _this.$store.state.appVersion = res.data.data.version;
          // 更新提示
          _this.showUpdataApp = true;
          _this.showVersionUpdate = true;
          _this.$store.state.uadataContent = res.data.data;
          _this.versionData = res.data.data;
          _this.appName = res.data.data.path;
        }
      }
    }

    this.popupHeight = "height:215px;";
    // var _this = this;
    // document.addEventListener("backbutton", onBackKeyDown, false);
    // function onBackKeyDown() {
    //   //可以加一些逻辑判断，比如可以在固定的几个页面按返回键时才切换到后台
    //   // 返回按钮事件的事件处理函数
    //   if (_this.$router.history.current.name === "Home") {
    //     _this.showMapData = false;
    //     _this.flag = false;
    //     if (exitApp === 0) {
    //       exitApp++;
    //       Toast("再点击一次回到主页");
    //       setTimeout(() => {
    //         exitApp = 0;
    //       }, 2000);
    //     } else if (exitApp === 1) {
    //       navigator.Backbutton.goHome(function() {}, function() {});
    //     }
    //   } else {
    //     _this.$router.go(-1);
    //   }
    // }
  },
  beforeDestroy() {
    // document.removeEventListener("backbutton", onBackKeyDown, false);
  }
};
</script>
<style scoped>
.tabBarFixed {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 57px;
  z-index: 9999;
  box-shadow: 0 0 10px #ccc;
  transition: height 0.5s, transform 0.5ss, show 0.5s;
  -moz-transition: height 0.5s, transform 0.5s, show 0.5s; /* Firefox 4 */
  -webkit-transition: height 0.5s, transform 0.5s, show 0.5s; /* Safari and Chrome */
  -o-transition: height 0.5s, transform 0.5s, show 0.5s; /* Opera */
}
.tabBarFixed span {
  border-bottom: 2px #3572fe solid;
  padding-bottom: 5px;
  color: #3572fe;
  font-weight: 600;
}
.popupDataContainer {
  padding: 15px;
}
.headerTitle {
  font-weight: 600;
  font-size: 16px;
  height: 40px;
  display: flex;
  align-items: center;
  position: absolute;
  width: 100%;
  padding-left: 15px;
  top: 0;
}
.mapContent {
  font-size: 12px;
  /* padding: 5px 10px; */
}
.flexBox {
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
}
.flexBox div {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  height: 30px;
  align-items: center;
  font-size: 12px;
}
.flexBox .title {
  /* font-weight: 600; */
  font-size: 12px;
  margin-right: 10px;
}
.imageSize img {
  width: 10px;
  height: 10px;
}
.bottomNavi {
  border-top: 1px rgb(231, 230, 230) solid;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.borderNavi {
  border-radius: 10px;
  border: 1px #ccc solid;
  padding: 4px 10px;
}
.subtext {
  font-size: 10px;
  color: #999999;
  /* height: 30px; */
}
.flexRow {
  display: flex;
  flex-direction: row;
  height: 57px;
  align-items: center;
  border-bottom: 1px rgb(231, 230, 230) solid;
  font-size: 12px;
}
.flexRow div {
  margin-right: 20px;
}
.dataDetails {
  border-top: 10px rgb(245, 245, 245) solid;
  padding: 15px;
}
.warningHeaderTitle {
  font-size: 14px;
  border-bottom: 1px rgb(231, 230, 230) solid;
  height: 30px;
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
}
.warningTypeContent {
  font-size: 12px;
}
.flexBoxWarning {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  height: 30px;
  align-items: center;
}
.flexBoxContentRealData {
  display: flex;
}
.flexBoxContentRealData div {
  flex: 1;
  display: flex;
  justify-content: center;
  height: 30px;
  align-items: center;
  font-size: 12px;
}
.dataHeader {
  font-size: 14px;
  font-weight: 600;
}
.css3Trasition {
  /* position: relative; */
  transition: height 0.2s, transform 0.2s;
  -moz-transition: height 0.2s, transform 0.2s; /* Firefox 4 */
  -webkit-transition: height 0.2s, transform 0.2s; /* Safari and Chrome */
  -o-transition: height 0.2s, transform 0.2s; /* Opera */
}
.heightStyle {
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding: 0 10px;
}
.boxUpdata {
  padding: 10px;
}
.updataHeader {
  height: 40px;
  border-bottom: 2px rgb(50, 142, 251) solid;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(50, 142, 251);
}
.justycont {
  justify-content: space-between;
}
</style>


